@import url('https://fonts.googleapis.com/css2?family=Lato&family=Raleway&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nixie+One&display=swap');body{
  background-color: #2f4454;
}
.App {
  text-align: center;

}
.d-flex{
  display: flex;
}
.w-100{
  width: 100%;
}
.mr-10{
  margin: 0 5px 0 0;
}
.mt-20{
  margin-top: 20px;
}
.text-left{
  text-align: left;
}

.page{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 65vh;
  box-shadow: 0px 0px 30px 0px rgba(255,255,255,1);
}
.navbar{
  position: sticky;
  left: 0; right: 0; top: 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 10vh;
  z-index: 1;
}

.navbar-mobile{
  display: none;
  position: sticky;
  left: 0; right: 0; top: 0;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 10vh;
  z-index: 1;
}
.navbar-mobile a{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3.3vh 12px 1vh 12px;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.navbar ul{
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-around;
  align-content: center;
  flex-wrap: wrap;
}

.navbar li{
  display: inline-block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  height: 40px;
  width: 20%;
  overflow: hidden;
  text-align: center;
}

.navbar li>span{
  display: flex;
  height: 85%;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.navbar li>span:nth-child(1){
  width: 100%;
  color: white;
  font-size: 25px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar li>span:nth-child(2){
  width: 100%;
  background: linear-gradient(180deg, rgba(47,68,84,0) 50%, rgba(74,37,44,1) 50%);
}

.navbar li:hover span{
  transform: translateY(-100%);
}

.navbar a{
  font-size: 14px;
  color: white;
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
}
.navbar-mobile a{
    font-size: 12px;
    padding: 2.5vh 12px 1vh 12px;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

.active2{
  padding: 3.3vh 12px 1vh 12px;
  background: rgb(47,68,84);
  background: linear-gradient(180deg, rgba(47,68,84,0.47942927170868344) 0%, rgba(74,37,44,1) 100%);
}
.active{
  padding: 0 10px 6px 10px;
  font-size: 25px;
  color: #fff;
  border-bottom: 3px solid white;
}
.homeImg{
  right: 0;
  bottom: 0;
  position: absolute;
  width: 65%;
  height: 55vh;
  background: linear-gradient(90deg, rgba(47,68,84,1) 0%, rgba(55,110,111,0.5550595238095238) 100%);
}
.homeImg img{
  height: 100%;
  animation-name: photo;
  animation-duration: 1s;
}
@keyframes photo {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}
.home-text{
  font-family: 'Lato', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: start;
  width:80%;
  height:330px;
  padding: 20px 20px 20px 70px;
  color: white;
  z-index: 1;
}
.home-text h1{
  padding: 0;
  margin: 0;
  font-family: 'Raleway', sans-serif;
}
.title-hover{
  animation: dubstep 1.2858s infinite ease;
}

@keyframes dubstep {
  0% {
    text-shadow: 0 0 0 #fff;
  }
  40% {
    text-shadow: 3px 0 0 #da7b93;
  }
  43% {
    text-shadow: 3px 0 0 #2E151B,
    15px 0 0 #da7b93,
    -7px 0 0 #376e6f;
    color: #f1f1f1;
  }
  44% {
    text-shadow: 3px 0 0 #2E151B,
    15px 2px 0 #da7b93,
    -7px -1.5px 0 #376e6f;
    color: rgba(0,0,0,0.2);
  }
  45% {
    text-shadow: 3px 0 0 #2E151B,
    15px 0 0 #da7b93,
    -7px 0 0 #376e6f;
    color: #f1f1f1;
  }


  100% {
    text-shadow: 0 0 0 #fff;
  }
}
.here-me{
  margin-top: 25px;
  width: 100px;
  padding: 5px;
  border: 3px solid #4a252c;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
  box-shadow: 5px 10px 10px 0 rgb(95, 42, 60);
}
.here-me:hover{
  background-color: #4a252c;
  border: none;
  color: #fff;
  padding: 8px;
}
.portfolio-main{
  margin-top: 35px;
  width: 97%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  color: white;
}
.portfolio-card{
  width: 29%;
  margin: 0 7px;
  cursor: pointer;
  transition: .4s;
}
.portfolio-card p{
  font-family: 'Raleway', sans-serif;
  margin: 5px;
}
.portfolio-card:hover{
  z-index: 1;
  margin-top: 23px;
  padding: 5px;
  transform: scale(1.7);
  box-shadow: 0px 1px 5px 0px rgba(255,255,255,1);
  background: linear-gradient(0deg, rgba(47,68,84,1) 0%, rgba(55,110,111,0.7091211484593838) 100%);
}
.portfolio-card img{
  width: 100%;
}
.portfolio-card a{
  text-decoration: none;
  color: white;
}
.goToCode{
  padding: 10px 20px;
  font-size: 10px;
  background-color: #1C3334;
  cursor: pointer;
  opacity: .4;
  transition: .3s;
}
.goToCode:hover{
  box-shadow: inset 0px 0px 5px 0px rgba(255,255,255,1);
  opacity: 1;
}
.about-text{
  font-family: 'Nixie One', cursive;
  margin-top: 30px;
  text-align: justify;
  color: white;
  padding: 0px 20px 20px 40px;
  width: 50%;
  height: 250px;
  overflow: auto;
}
.about-link {
  color: white;
  text-decoration: none;
  display: inline-block;
  transition: color 250ms, text-shadow 250ms;
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 1);
  position: relative;
  z-index: 0;
}

.about-link:after {
   position: absolute;
   z-index: -1;
   bottom: -5px;
   left: 50%;
   transform: translateX(-50%);
   content: '';
   width: 100%;
   height: 3px;
   background-color: #4a252c;
   transition: all 250ms;
}

.about-link:hover {
  color: white;
  text-shadow: 0px 1px 0px rgb(83, 35, 35);
}

.about-link:hover::after {
   height: 105%;
   width: 105%;
}
.about-icon-div{
  margin-top: 10px;
  font-family: 'Lato', sans-serif;
  color: white;
  width: 40%;
  padding: 10px;
}
.about-icon-div p{
  margin: 0px;
  text-shadow: 1px 2px 3px white;
}
.about-icon{
  width: 100%;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
}
.about-icon i{
  font-size: 40px;
  margin: 10px;
  transition: .3s;
}
.cv{
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lato', cursive;
  margin-top: 25px;
  width: 100px;
  padding: 5px;
  border: 3px solid #4a252c;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
  box-shadow: 5px 10px 10px 0 rgb(95, 42, 60);
}
.cv:hover{
  background-color: #4a252c;
  border: none;
  color: #fff;
  padding: 8px;
}
.icon{
  font-size: 40px;
  margin: 10px;
  transition: .3s;
}
.icon:hover{
  transform: scale(1.6);
}

.about-icon span{
  position: absolute;
  font-family: 'Raleway', sans-serif;
  font-size: 10px;
}
.mongoImg{
  margin: 5px;
  height: 40px;
  transition: .3s;
}
.mongoImg:hover{
  transform: scale(1.6);
}
.contact-location{
  font-family: 'Raleway', sans-serif;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.location{
  font-size: 30px;
  margin-bottom: 5px;
  color: white;
}
.contact-location p{
  margin-top: 0px;
  font-size: 20px;
}
.contact-icon{
  display: flex;
  justify-content: center;
  color: white;
  font-size: 50px;
}
.contact-icon a{
  margin: 0 20px;
}
.contact-icon a{
  color: white;
}
.contact-icon p{
  padding: 0;
  margin: 0;
}
.fb{
  cursor: pointer;
  transition: 1s;
}
.fb:hover{
  color: #0E8EF2;
  transform: rotate(360deg);
}
.linkedin{
  cursor: pointer;
  transition: 1s;
}
.linkedin:hover{
  color: #0077B5;
  transform: rotate(360deg);
}
.instagram{
  cursor: pointer;
  transition: 1s;
}
.instagram:hover {
  transform: rotate(360deg);
  color: #ffffff;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.drop{
  font-size: 10px;
  color: #e5c7d2;
  animation-name: drop;
  animation-duration: 7s;
  animation-iteration-count: infinite;
  opacity: 0;
}
@keyframes drop {
  0%{
    opacity: 0;
  }
 50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}

.circle1{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 75%;
  top: 5%;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: #4a252c;
  transition: .1s;
  cursor: grab;
  animation-name: anim1;
  animation-duration: 1s;
  animation-direction: alternate;
  box-shadow: inset -2px 0px 5px 0px rgba(219,219,219,1);
}
@keyframes anim1 {
  0% {
    opacity: 0;
    transform: translateY(-20vw);
  }
  100% {
    opacity: 1;
    transform: translateY(0vw);
  }

}
.circle2{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 17%;
  top: 77%;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #4a252c;
  transition: .1s;
  cursor: grab;
  animation-name: anim2;
  animation-duration: 1s;
  animation-direction: alternate;
  box-shadow: inset 0px -2px 4px 0px rgba(219,219,219,1);
}
@keyframes anim2 {
  0% {
    opacity: 0;
    transform: translateY(5vw);
  }
  100% {
    opacity: 1;
    transform: translateY(0vw);
  }
}
.circle3{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 15%;
  top: 12%;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #1C3334;
  transition: .1s;
  cursor: grab;
  animation-name: anim3;
  animation-duration: 1s;
  animation-direction: alternate;
  box-shadow: inset 0px 2px 5px 0px rgba(219,219,219,1);
}
@keyframes anim3 {
  0% {
    opacity: 0;
    transform: translateX(-50vw);
  }
  100% {
    opacity: 1;
    transform: translateX(0vw);
  }
}
.circle4{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left:65%;
  top: 78%;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: #1C3334;
  transition: .1s;
  cursor: grab;
  animation-name: anim4;
  animation-duration: 1s;
  animation-direction: alternate;
  box-shadow: inset -1px 0px 3px 0px rgba(219,219,219,1);
}
@keyframes anim4 {
  0% {
    opacity: 0;
    transform: translateX(20vw);
  }
  100% {
    opacity: 1;
    transform: translateX(0vw);
  }
}
.circle5{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left:79%;
  top: 60%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #a97d8b;
  transition: .1s;
  cursor: grab;
  opacity: 0.5;
  animation-name: anim5;
  animation-duration: 1s;
  animation-direction: alternate;
  box-shadow: inset -1px 0px 2px 0px rgba(219,219,219,1);
}
@keyframes anim5 {
  0% {
    opacity: 0;
    transform: translateX(-20vw);
  }
  100% {
    opacity: 0.5;
    transform: translateX(0vw);
  }
}

.fade-appear,
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms linear 150ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms linear;

}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(156, 212, 241, 0.3);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(62, 182, 241, 0.5);
}

@media only screen and (max-width: 400px) {

  .App{
    width: 97%;
    height: 97vh;
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

  }
  .page{
    margin-top: 0;
    width: 90%;
    height: 89vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .home-text{
    width: 80%;
    position: absolute;
    top: 15%;
    left: -7%;
    z-index: 1;
  }
  .mobile-size{
    margin-top: 5px;
  }
  .mobile{
    flex-direction: column;
  }
  .homeImg{
    position: absolute;
    left: 36%;
    bottom: 0;
    height: 55vh;
    background: none;
  }

  .navbar{
    display: none;
  }
  .navbar-mobile{
    display: flex;
  }
  .navbar a{
    padding: 0 15px;
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 12px;
  }
  .circle1 {
    left: 52%;
    top: 25%;
    opacity: 0.7;
  }
  .circle4 {
    position: absolute;
    left: 30%;
    top:85%;
    opacity: 0.5;
  }
  .circle2 {
    position: absolute;
    left: 4%;
    top: 65%;
    opacity: 0.5;
  }
  .about-text{
    width: 88%;
    height: 45vh;
    padding: 0px 20px;
  }

  .about-icon-div{
    margin-top: 0px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .about-icon{
    align-items: center;
    justify-content: center;

  }
  .mobile-about-text{
    font-weight: bold;
    font-size: 15px;
    text-shadow: 2px 5px 5px lightblue;
  }
  .portfolio-main{
    margin: 0;
    width: 100%;
    height: 75vh;
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .portfolio-card{
    margin-top: 0px;
    margin-bottom: 10px;
    width: 90%;
  }
  .portfolio-card:hover{
    transform: scale(1.0);
    box-shadow: none;
    background: none;
  }
  .goToCode{
    opacity: 1;
  }
  .icon{
    font-size: 35px;
  }
  .here-me{
    position: absolute;
    top: 60%;
  }
  .drop{
    display: none;
  }

}